import React from 'react';
import 'antd-mobile/dist/antd-mobile.css';
import {Button,TabBar} from 'antd-mobile';
import {withRouter} from 'react-router-dom';
class Mytabs extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
		selectedTab: 'gouwuche',
	  };
	}
  
	render() {
		const {pathname} =this.props.location;
		console.log(pathname);
		if(pathname.split('/').length>2){
			return null
		}
	  return (
		<div style={{ position: 'fixed', width: '100%', bottom: 0 }}>
		  <TabBar
			unselectedTintColor="#949494"
			tintColor="#33A3F4"  //选中字体颜色
			barTintColor="white"  //背景颜色
		  >
			<TabBar.Item
			  title="首页"
			  key="home"
			  icon={<i className='iconfont icon-Homehomepagemenu'></i>}
			  selectedIcon={<i className='iconfont icon-Homehomepagemenu1'></i>}
			  selected={pathname === '/'}
			  onPress={() => {
				  this.props.history.push('/')
			  }
			  }
			  data-seed="logId"
			>
			</TabBar.Item>
			  
			<TabBar.Item
			  icon={<i className='iconfont icon-gouwuche'></i>}
			  selectedIcon={<i className='iconfont icon-ziyuan'></i>}
			  title="购物车"
			  key="Gouwuche"
			  selected={pathname === '/gouwuche'}
			  onPress={() => {
				this.props.history.push('/gouwuche');
			  }}
			  data-seed="logId1"
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={ <i className='iconfont icon-dingdan1'></i>}
			  selectedIcon={<i className='iconfont icon-dingdan'></i>}
			  title="订单列表"
			  key="List"
			  selected={pathname === '/list'}
			  onPress={() => {
				this.props.history.push('/list');
			  }}
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={<i className='iconfont icon-wode'></i>}
			  selectedIcon={ <i className='iconfont icon-wodedangxuan'></i>}
			  title="我的淘宝"
			  key="my"
			  selected={pathname === '/my'}
			  onPress={() => {
				this.props.history.push('/my');
			  }}
			>
			</TabBar.Item>
		  </TabBar>
		</div>
	  );
	}
  }
export default withRouter(Mytabs);