import React, { Component } from 'react'
import MytabDetail from './MytabDetail'
import {HashRouter as Router,Redirect,Route,NavLink,Switch,privateRoute} from 'react-router-dom'
import {NavBar,Icon} from 'antd-mobile'

export default class Detail extends Component {
    render() {
        return (
            <div>
                <NavBar
                        mode="light"
                        icon={<Icon type="left" />}
                        onLeftClick={() => this.props.history.push('/')}
                    >商品详情</NavBar>
                 <img src={"https://gw1.alicdn.com/bao/uploaded/i1/2654954038/O1CN014Wjb7D1fhRwLzfgze_!!2654954038.jpg_100x100.jpg"} style={{width
          :'100%'}} />
         
            <Router>
                <MytabDetail />
            </Router>
          
		</div>
          
          
    
        )
    }
}
