import React from 'react';
import './detail.css';
import 'antd-mobile/dist/antd-mobile.css';
import {Button,TabBar} from 'antd-mobile';
import {withRouter} from 'react-router-dom';
class MytabDetail extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
		selectedTab: 'dianpu',
	  };
	}
  
	render() {
		const {pathname} =this.props.location;
	  return (
		<div style={{ position: 'fixed', width: '100%', bottom:0 }}>
		  <TabBar
			unselectedTintColor="#949494"
			tintColor="rgb(255, 92, 18)"  //选中字体颜色
			barTintColor="white"  //背景颜色
		  >
			<TabBar.Item
			  icon={<i className='iconfont icon-dianpu'></i>}
			  selectedIcon={<i className='iconfont icon-dianpu'></i>}
			  title="店铺"
			  key="dianpu"
			  selected={pathname === '/dianpu'}
			  onPress={() => {
				console.log('dianpu')
			  }
			  }
			  data-seed="logId"
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={<i className='iconfont icon-kefu'></i>}
			  selectedIcon={<i className='iconfont icon-kefu'></i>}
			  title="客服"
			  key="kefu"
			  selected={pathname === '/kefu'}
			  onPress={() => {
				console.log('kefu')
			  }
			  }
			  data-seed="logId1"
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={ <i className='iconfont icon-shoucang'></i>}
			  selectedIcon={<i className='iconfont icon-shoucang'></i>}
			  title="收藏"
			  key="shoucang"  
			  selected={pathname === '/shoucang'}
			  onPress={() => {
				console.log('shoucang')
			  }
			  }
			  data-seed="logId1"
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={<button className="left">加入购物车</button>}
			  key="jiagou"  
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={<button className="right">立即购买</button>}
			  key="buy"  
			>
			</TabBar.Item>
		  </TabBar>
		</div>
	  );
	}
  }
export default withRouter(MytabDetail);